* {
  margin: 0;
  padding: 0;
}

.login-main {
  align-items: center;
  background-color: rgb(24, 25, 26);
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.content-container {
  align-items: center;
  background-color: white;
  border: rgb(29, 185, 83) solid 4px;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  height: 45vh;
  padding: 20px;
  width: 50vh;
}

.login-title {
  color: rgb(29, 185, 83);
  font-size: 55px;
  height: 20%;
  margin-top: 15px;
}

.login-form {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  width: 100%;
}

.login-label {
  text-align: center;
  width: 100%;
}

.login-input {
  background-color: rgb(232, 240, 254);
  border: rgb(29, 185, 83) 2px solid;
  border-radius: 5px;
  font-family: monospace;
  font-size: 15px;
  height: 40px;
  margin-bottom: 30px;
  padding-left: 15px;
  width: 75%;
}

.login-button-enabled {
  background-color: rgb(29, 185, 83, 1);
  border: rgb(29, 185, 83) 1px solid;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  font-size: 20px;
  height: 50px;
  transition-duration: 0.5s;
  width: 75%;
}

.login-button-disabled {
  background-color: rgb(29, 185, 83, 0.5);
  border: rgb(29, 185, 83) 1px solid;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  font-size: 20px;
  height: 50px;
  transition-duration: 0.5s;
  width: 75%;
}

.login-button-enabled:hover {
  background-color: rgb(29, 165, 76);
  font-size: 25px;
}
